html:has(.main-content-3){--bg: #efefef;--primary: #d11313;background-color:var(--bg);position:relative;z-index:1}html:has(.main-content-3)::after,html:has(.main-content-3)::before{content:"";position:absolute;left:0;width:100%}html:has(.main-content-3)::after{top:0;z-index:-2;height:100%;background:linear-gradient(90deg, #fff 25%, transparent 25%, transparent 75%, #fff 75%, #fff),linear-gradient(90deg, #fff 25%, transparent 25%, transparent 75%, #fff 75%, #fff);background-size:60px 60px;opacity:.5}html:has(.main-content-3)::before{bottom:0;z-index:-1;height:100%;background:var(--bg);background:linear-gradient(0deg, var(--bg) 0%, rgba(239, 239, 239, 0.5) 100%, rgba(255, 255, 255, 0) 100%)}@media(max-width: 575px){.no-transform-mobile span{transform:none !important}}.animate-scroll-down{background:linear-gradient(to bottom, rgb(255, 255, 255) 50%, rgba(255, 255, 255, 0) 50%);background-position:0 -40px;background-size:100% 200%;animation:scrolldown 2.2s cubic-bezier(0.76, 0, 0.3, 1) forwards infinite}@keyframes scrolldown{0%{background-position:0 -40px}75%{background-position:0 0}100%{background-position:0 40px}}